
.copmonent{
    position: relative;
    background-color: white;
    width: 240px;
    font-size: 12px;
}

.itemContainer{
    margin-right: 10px;
}
.itemContainer >div {
    width: 100%;
    vertical-align: center;
    display: flex;
    align-items: center;
    margin: 24px;
    display: flex;
    align-items: center;

    font-family: Montserrat, serif;
    font-style: normal;
    font-weight: 500;
    color: #6D7895;
}
.itemContainer >div >img{
    margin-right:  14px;
}


.arrow{
    height: 30px;
    position: absolute;
    bottom: 20px;
    right: 40px;
    cursor: pointer;
}






