.container{
    position: relative;
    display: flex;
    height: 56px;
    background-color: white;
    width: 320px;
    border-radius: 8px;
    align-items: center;

}
.date{
    background: #EDF2FF;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    justify-self: center;
    align-items: center;
    padding: 8px;
    margin: 2px 8px;

}
.date >div{
    line-height: 8px;
}

.text{
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 24px;
    color: #303842;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
.text >div{
    line-height: 15px;
}
.candidat{
    font-family: Montserrat, serif;
    font-style: normal;
    align-items: center;
    color: #6D7895;
}
.mark{
    contain: content;
    aspect-ratio: 1;
    position: absolute;
    right: 0;
    height: 80%;
    top:20%;
    cursor: pointer;
}
