




.cardContainer{
    font-family: Montserrat, serif;
    font-style: normal;
    line-height: 24px;
    display: flex;
    flex-direction: column;
    align-items: center;

}

.avasContainer >img{
    border-radius: 50%;
    margin-right: -5px;
}
.tittle{

    font-weight: 600;
    font-size: 14px;
    /* identical to box height, or 171% */

    display: flex;
    align-items: center;
    text-align: center;
    color: #303842;
}
.img{
    margin-top: 25px;
    margin-bottom: 18px;
}
.text{

    font-weight: normal;
    font-size: 12px;

    display: flex;
    align-items: center;
    text-align: center;
    color: #6D7895;
}



