
.container{
    display: flex;
    align-items: center;
    margin: 18px 20px;

}
.imageContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    overflow: hidden;
    contain: content;
    width: 50px;
    aspect-ratio: 1;
}
.imageContainer >img{
    width: 100%;
    height: 100%;
}

.texts{
    font-style: normal;
    font-family: Montserrat, serif;
    font-size: 12px;
    line-height: 15px;
    margin-left: 5px;
}
.name{
    font-weight: 600;
    color: #303842;
}
.role {
    display: flex;
    align-items: center;
    color: #6D7895;
}








