



.container{
    position: relative;
    background-color: #8d8bed;
    width: 100%;
    height: 192px;
    border-radius: 8px;

}


.image{
    position: absolute;
    height: 256px;
    right: 10%;
    bottom: 0;
    z-index: 2;
}



.hi,.hiText,.closeText{
    display: block;
    /*margin: 12px;*/
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 24px;
    color: #FFFFFF;
}
.hi{
   margin: 12px;
}

.hiText{
    font-size: 14px;
    width: 60%;
    margin: 0 12px  12px;

}
.closeText{
    left: 40px;
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    text-decoration-line: underline;
    margin: 0 12px  12px;
    cursor: pointer;
    width: min-content;

}



